<template>
  <button @click="infoClick()"><img src="../img/info.png" alt=""></button>
</template>

<script>
export default {
  setup(props) {
    let tkey = props.params.value.toString();
    let name = props.params.data.name.toString();
    let info = props.params.data.infoWorkPlaces.toString();
    let year = props.params.data.year.toString();
    let hours = props.params.data.hours.toString();
    function infoClick()
    {
      window.location.href = '/filtered?tkey=' + this.tkey + '&name=' + this.name + '&info=' +
          this.info + '&year=' + this.year + '&hours=' + this.hours;
    }
    return {
      tkey, infoClick, name, info, year, hours
    };
  }
}
</script>

<style scoped>
img {
  width: 28px;
}
button {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 80px;
  padding: 0;
  border: none;
  background-color: transparent;
  cursor: pointer;
}
</style>